/*------------------------------------*\
    #TEXT
\*------------------------------------*/


@font-face {
 font-family: 'Bluu';
 src: url("../fonts/BluuNext-Bold.otf") format("opentype");
}

@font-face {
 font-family: 'Triplex';
 src: url("../fonts/TriplBol.otf") format("opentype");
}

@font-face {
 font-family: 'Din';
 src: url("../fonts/DINReg.otf") format("opentype");
}

/**
 * Paragraph base styles
 */
p {
	margin-bottom: $space;
}

h1{
	font-family: 'Bluu';
}

h2{
	font-family: 'Bluu';
}

h3{
	font-family: 'Triplex';
}
/**
 * Blockquote base styles
 */
blockquote {
    font-style:italic;
    border-left: 1px solid $color-gray-50;
    color: $color-gray-50;
	padding-left: $pad;
	margin-bottom: $space;
}

/**
 * Horizontal rule base styles
 */
hr {
	border: 0;
    height: 2px;
    background: $color-gray-15;
    margin: $space-double 0;
}

/**
 * Abbreviation base styles
 */
abbr {
    border-bottom: 1px dotted $color-gray-50;
    cursor: help;
}
