/*------------------------------------*\
    #TEXT
\*------------------------------------*/

/**
 * Passages of text
 */
.text {
	a {
		text-decoration: underline;
	}

	ul {
		list-style: disc;
		margin: 0 0 $space 1.2em;

		ul {
			margin-bottom: 0;
		}
	}

	ol {
		list-style: decimal;
		margin: 0 0 $space 1.5em;

		ol {
			margin-bottom: 0;
		}
	}
}

/**
 * Text passage
 */

 .s-text-passage {
	 margin-bottom: 1rem;
 }

/**
 * Image within text passage
 */
.c-text-passage__img {
	border-radius: $border-radius;
	margin-bottom: 1rem;
}





/*HEADER MOLLECULES*/
.menu-head{
	width: auto;
}

.menu-head ul{
	display: flex;
	    flex-direction: row;
	    flex-wrap: wrap;
	    justify-content: space-around;
	    align-content: flex-start;
}

.menu-head li{
	margin-left: 25px;
		margin-right: 25px;
}
.menu-head h1{
	font-size: 25px;
	color: #172622;
}


.picto-header ul{

	display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: space-around;
			align-content: flex-start;
			margin: 25px;
}



/*HEADER*/
.header-nav{
	position: fixed;
	z-index: 100;
	margin: 0;
	width: 100%;
	background: #FFFFFF;
	box-shadow: 7px 7px 7px rgba(68, 68, 68, 0.2);
}

.header-nav ul{
	width: 100%;
	display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: space-around;
			align-content: flex-start;
			align-items: center;
			margin-top: 10px;
					margin-bottom: 10px;
}




/*IMG HERO*/
.img-hero img {
	width: 100%;
	display: block;
}

.image-h-container {
	display: block;
	position: relative;
}


.image-h-container::after {
	position: absolute;
	content : " ";
	top : 0;
	bottom : 0;
	left : 0;
	right : 0;
	transition: all 1s;
		background-color: rgba(56, 152, 212, 0.8);
}

.legende-hero{
	margin: 15%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	position: absolute;
	z-index: 38;
	color:#000000;
}





